import styled from "styled-components"

export const Cover = styled.div`
  display: flex;
  position: relative;
  width: 100%;
  height: ${props => props.height};
  user-select: none;
  @media (max-width: 576px) {
    height: 320px;
  }
  .text-wrapper {
    position: absolute;
    top: ${props => props.textTop};
    bottom: ${props => props.textBottom};
    left: 0;
    right: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
    padding-left: 16px;
    padding-right: 16px;
    color: ${({ theme }) => theme.text_primary};
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    span,
    p {
      :nth-of-type(1n) {
        @media (max-width: 576px) {
          font-size: 28px;
          line-height: 34px;
        }
      }
      :nth-of-type(2n) {
        @media (max-width: 576px) {
          font-size: 22px;
          line-height: 28px;
        }
      }
    }
  }
  .author-avatar-wrapper {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: max-content;
    bottom: -64px;
    .cover-site-author__avatar {
      z-index: 2;
      width: 128px;
      border-radius: 100%;
    }
    .cover-site-author__circle {
      position: absolute;
      z-index: 1;
      display: flex;
      border: 1px dashed ${({ theme }) => theme.background_primary};
      border-radius: 100%;
      @media print {
        display: none;
      }
      :nth-of-type(1n) {
        width: 192px;
        height: 192px;
        opacity: 0.5;
        animation: spinRight 40s linear infinite;
      }
      :nth-of-type(2n) {
        width: 256px;
        height: 256px;
        opacity: 0.3;
        animation: spinLeft 80s linear infinite;
      }
      :nth-of-type(3n) {
        width: 320px;
        height: 320px;
        opacity: 0.2;
        animation: spinRight 40s linear infinite;
      }
      :nth-of-type(4n) {
        width: 384px;
        height: 384px;
        opacity: 0.1;
        animation: spinLeft 80s linear infinite;
      }
    }
  }
  .cover-bg {
    display: flex;
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: ${({ theme }) => theme.background_secondary};
    @media print {
      filter: grayscale(100%);
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque},
        inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
        inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
        inset 1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
    }
  }
  .cover-bg-wrapper {
    position: absolute;
    bottom: 0;
    width: 100%;
    min-width: 1024px;
    @media print {
      opacity: 0.4;
    }
  }

  @keyframes spinRight {
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes spinLeft {
    100% {
      transform: rotate(-360deg);
    }
  }
`
